
.drop-area,
.over {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
}
.drop-area {
  position: relative;
  border: #6c938a dotted 2px;
  min-height: 10em;
  text-align: center;
  margin-bottom: 0.5em;
}
.drop-area small {
  color: #6c938a;
}
.over {
  background-color: rgba(227,168,38,0.5);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 105;
  pointer-events: none;
}
.dropped {
  border: #1aad8d dashed 1px;
  margin: 0.125em;
  background-color: #fff;
  cursor: pointer;
}
.dropped:hover {
  border-color: #e3a826;
}
.dropped:hover .icon svg {
  fill: #e3a826;
}
.dropped .name {
  font-size: 0.6em;
  margin: 0 0.5em 0.25em 0.5em;
}
.dropped .icon,
.dropped .icon svg {
  margin: 0;
  padding: 0;
}
.dropped .icon svg {
  fill: #1aad8d;
  background: transparent;
}


